import { NgModule, Component, Input } from '@angular/core';
import { NavParams, ModalController, LoadingController } from '@ionic/angular';

import { Category, Game } from '../../providers/providers';

@Component({
  selector: 'change-category-modal-page',
  templateUrl: 'change-category-modal-page.html',
  styleUrls: ['change-category-modal-page.scss'],
})
export class ModalPage {

  // Data passed in by componentProps
  @Input() categories: Category[];

  loading: any;
  isLoading = false;

  constructor(public game: Game, public modalCtlr: ModalController, public loadingCtrl: LoadingController) {
  }

  closeModal() {
    this.modalCtlr.dismiss();
  }

  async chooseCategory(category: Category) {
    this.isLoading = true;
    return await this.loadingCtrl.create({
      message: '<div class="loading-category text-center">' +
        '<div><img class="rotating" src="../../assets/img/categories/' + category.icon + '.png"/></div>' +
        '<div class="bon-courage">Bon courage ...</div>' +
        '</div>',
      cssClass: 'loading-category',
      duration: 5000,
    }).then(a => {
      a.present().then(() => {
        console.log('presented');
        this.game.initQuestions(category);
        this.game.loadQuestions(20, true);
        console.log("chargés");
        this.dismiss();
        this.closeModal();
      });
    });
  }

  async dismiss() {
    this.isLoading = false;
    return await this.loadingCtrl.dismiss().then(() => console.log('dismissed'));
  }

}
