import { NgModule, Component, ViewChild } from '@angular/core';
import { AlertController, ToastController, ModalController, NavParams, LoadingController } from '@ionic/angular';

import {
  trigger,
  state,
  style,
  animate,
  transition,
  keyframes
} from '@angular/animations';

import { Api, Question, Answer, Category, Categories, Game, Database } from '../../providers/providers';
import { Event } from '../../enums/boEvents.enums';

import { ModalPage } from './change-category.modal';

@Component({
  selector: 'game',
  templateUrl: 'tab-game.page.html',
  styleUrls: ['tab-game.page.scss'],
  animations: [
    trigger('cardRectoState', [
      state('false', style({
        transform: 'rotateY(-180deg)',
      })),
      transition('* => false', animate(500, keyframes(
        [
          style({ transform: 'rotateY(0deg)', offset: 0.5 }),
          style({ transform: 'rotateY(-180deg)', offset: 1 })
        ]),
      ))
    ])
  ]
})
export class TabGamePage {
  @ViewChild('cardSlides', { 'read': null, 'static': false }) slides: any;

  slideOpts: any;

  maxSlideViewed: number = 5;

  isLoading = false;

  // Appelé qu'une seule fois
  constructor(public api: Api,
    public categories: Categories,
    public game: Game,
    public database: Database,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public modalCtlr: ModalController,
    public loadingCtrl: LoadingController) {
    alert("5");

    this.database.loadDB().then(() => {
      alert("6");
      this.database.initDB('fr').then(() => {
        alert("7");
        this.game.initQuestions(null);
        alert("8");
        this.game.loadQuestions(20);
        alert("9");
        this.database.startAsynTasks();
        alert("10");
      });
    });


    this.slideOpts = {
      'slidesPerView': 1
    }

  }

  ionViewWillLeave() {
    console.log('On quitte la page de jeu');
  }

  selectAnswer(question: Question, answer: Answer, answerIndex: number) {
    if (question.state) {
      console.log('click');
      answer.selected = true;
      this.database.jtp.by('id', question.id).state = false;
      question.state = false;
      if (answerIndex == 1)
        this.callEvent(question, Event.ANSWER1);
      else
        this.callEvent(question, Event.ANSWER2);
    } else {
      console.log("déjà joué");
    }
  }

  vote(question: Question, vote: boolean) {
    if (!question.voted) {
      if (vote) {
        question.nbLike += 1;
        this.callEvent(question, Event.LIKE);
      } else {
        question.nbDislike += 1;
        this.callEvent(question, Event.DISLIKE);
      }
      question.voted = true;
      console.log(question.id, vote);
    } else {
      console.log('Déjà voté');
    }
  }

  async signal(question: Question) {
    const alert = await this.alertCtrl.create({
      header: 'Signaler',
      inputs: [
        {
          name: 'signal',
          type: 'radio',
          label: 'Trop de fautes',
          value: Event.SIGNAL1,
          checked: true
        },
        {
          name: 'signal',
          type: 'radio',
          label: 'Contenu trop violent',
          value: Event.SIGNAL2,
          checked: false
        },
        {
          name: 'signal',
          type: 'radio',
          label: 'Mauvaise catégorie',
          value: Event.SIGNAL3,
          checked: false
        }],
      buttons: [
        {
          text: 'Annuler',
          role: 'cancel',
          handler: () => {
            console.log('Cancel');
          }
        },
        {
          text: 'Signaler',
          handler: (event) => {
            this.callEvent(question, event);
            this.toast();
          }
        }
      ]
    });

    await alert.present();
  }

  async changeCategory() {

    const modal = await this.modalCtlr.create({
      showBackdrop: true,
      animated: true,
      component: ModalPage,
      componentProps: {
        'categories': this.categories.categories
      }
    });
    return await modal.present();
  }

  async toast() {
    const toast = await this.toastCtrl.create({
      message: 'Merci !',
      duration: 2500,
      position: 'bottom',
      animated: true
    });
    toast.present();
  }

  share(question: Question) {
    console.log(question.id);
  }

  callEvent(question: Question, event: Event) {
    console.log("--> callEvent " + event);
    let param = { id: 0, event: 0 };
    param.id = question.id;
    param.event = event;
    this.api.post('event', param)
      .subscribe((res: any) => {
        console.log(res);
        console.log("<-- callEvent");
      }, err => {
        console.log(err);
        //console.error('ERROR', err);
      });
  }

  async showCategory(category: Category) {
    const alert = await this.alertCtrl.create({
      header: 'Catégorie',
      message: '<div class="alert-category">' +
        '<img src="../../assets/img/categories/' + category.icon + '.png"/>' +
        '<div class="category-block"><div class="margin-auto">' + category.label + '</div></div>' +
        '</div>',
      buttons: [
        {
          text: 'Je ne veux faire que ca !',
          handler: async () => {
            const a = await this.loadingCtrl.create({
              message: '<div class="loading-category text-center">' +
                '<div><img class="rotating" src="../../assets/img/categories/' + category.icon + '.png"/></div>' +
                '<div class="bon-courage">Bon courage ...</div>' +
                '</div>',
              cssClass: 'loading-category',
              duration: 5000,
            });
            a.present().then(() => {
              console.log('presented');
              this.game.initQuestions(category);
              this.game.loadQuestions(20, true);
              console.log("chargés");
              this.dismiss();
            });
          }
        },
        {
          text: 'Choisir une autre catégorie',
          handler: () => {
            this.changeCategory();
          }
        },
        {
          text: 'Retour'
        }
      ]
    });

    await alert.present();
  }

  onSlideChange() {
    this.slides.getActiveIndex().then(index => {
      if (index > this.maxSlideViewed) {
        while (index != this.maxSlideViewed) {
          this.slides.el.swiper.removeSlide(0);
          index--;
        }
      }
      if (this.slides.el.swiper.slides.length <= 15) {
        console.log("on ajoute des questions")
        this.game.loadQuestions(20);
      }
    });
  }

  async dismiss() {
    this.isLoading = false;
    return await this.loadingCtrl.dismiss().then(() => console.log('dismissed'));
  }
}
